.addContact {
  border: none;
  border-radius: 50px;
  background: rgb(0, 219, 73);
  box-shadow: 10px 10px 20px #827f7f, -10px -10px 20px #ffffff;
  padding: 10px 12px;
  font-size: 0.875rem;
  font-weight: 700;
  color: #3b3b3b;
  text-transform: uppercase;
  cursor: pointer;
  display: flex;
  align-items: center;
  column-gap: 10px;
  transition: all 400ms ease;
}

.addContact:focus, .addContact:hover {
  transform: rotate(360deg);
}

.searchShowBtn {
  border: none;
  border-radius: 50px;
  background: rgb(117, 117, 219);
  box-shadow: 10px 10px 20px #827f7f, -10px -10px 20px #ffffff;
  padding: 10px;
  font-size: 0.875rem;
  font-weight: 700;
  color: #3b3b3b;
  text-transform: uppercase;
  cursor: pointer;
  display: flex;
  align-items: center;
  column-gap: 10px;
  transition: all 400ms ease;
}

.searchShowBtn:focus, .searchShowBtn:hover {
  transform: rotate(360deg);
}

.buttons {
  width: 90%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0 auto;
}